
.md {
  font-size: 15px;
  color: #333;
  line-height: 1.7em;
}
.md h1,
.md h2,
.md h3,
.md h4 {
  font-weight: bold;
  color: #333;
  margin-left: -3px;
  line-height: 1.6em;
}
.md img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}
.md hr {
  border: 0;
  background-color: #eee;
  height: 1px;
  margin: 16px 0;
}
.md h1 {
  font-size: 30px;
  border-bottom: #eee 1px solid;
  padding-bottom: 8px;
}
.md h2 {
  font-size: 24px;
  border-bottom: #eee 1px solid;
  padding-bottom: 8px;
  margin-bottom: 30px;
  padding-top: 30px;
}
.md h3 {
  font-size: 18px;
  padding: 10px 0;
}
.md h4 {
  font-size: 16px;
}
.md blockquote {
  border-left-color: #009688;
  padding: 16px 10px 16px 24px;
  margin: 24px 0px;
  background: #f3f3f3;
  font-size: 15px;
  font-weight: normal;
/*color:#214750;*/
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
}
.md blockquote p {
  margin: 0;
}
@media screen and (max-width: 900px) {
.md blockquote {
    padding: 8px 10px 8px 15px;
    font-size: 14px;
}
}
.md ol,
.md ul {
  margin: 20px 0;
}
.md li {
  line-height: 2em;
}
.md pre {
  position: relative;
  margin-bottom: 24px;
  border-radius: 3px;
  background-color: #f3f3f3 !important;
  white-space: pre;
  display: block;
  padding: 12px 20px;
  line-height: 1.6em;
  overflow-x: auto;
}
.md pre code {
  white-space: pre;
  color: #34495e;
  font-weight: normal !important;
  line-height: 1.8em;
  font-size: 13px;
}
.md code {
  font-family: "SFMono-Regular", Menlo, 'Roboto Mono', Monaco, courier, monospace;
  background-color: #f3f3f3 !important;
  -webkit-font-smoothing: initial;
  padding: 0 2px;
/*font-weight normal !important;*/
}
.md code:after,
.md code:before,
.md kbd:after,
.md kbd:before {
  content: "" !important;
}
.md table {
  margin: 20px 0;
  overflow: auto;
  border-collapse: collapse;
}
.md th {
  border: #ccc 1px solid;
  text-align: left;
  background: #eee;
  padding: 8px 12px;
  white-space: nowrap;
}
.md td {
  border: #ccc 1px solid;
  padding: 8px 12px;
}
.md tr:nth-child(even) {
  background-color: #f6f6f6;
}
@media screen and (max-width: 600px) {
.md blockquote {
    padding: 8px 10px 8px 15px;
    font-size: 14px;
}
.md table {
    display: block;
}
.md pre {
    padding: 12px;
}
.md pre code {
    font-size: 12px;
}
}


