
.document-app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
}
.sidebar {
  width: 280px;
  background: #f5f5f5;
  border-right: 1px solid #ddd;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  position: fixed;
  height: calc(100vh - 65px);
  z-index: 1000;
  top: 65px;
  overflow-y: auto;
}
.sidebar:not(.sidebar-open) {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.sidebar-header {
  padding: 15px;
  border-bottom: 1px solid #ddd;
  text-align: right;
}
.close-btn {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: #666;
}
.menu-section {
  padding: 10px 0;
}
.menu-item,
.menu-group-header {
  padding: 12px 20px;
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: background 0.2s;
  transition: background 0.2s;
  white-space: nowrap;
}
.menu-item:hover,
.menu-group-header:hover {
  background: #e0e0e0;
}
.menu-icon {
  margin-right: 10px;
  width: 20px;
  text-align: center;
}
.menu-title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.arrow {
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}
.arrow-down {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.submenu {
  background: #fafafa;
}
.submenu-item {
  padding: 10px 20px 10px 50px;
  cursor: pointer;
  -webkit-transition: background 0.2s;
  transition: background 0.2s;
}
.submenu-item:hover {
  background: #e8e8e8;
}
.submenu-item.active {
  background: #009688;
  color: #fff;
}
.main-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-left: 280px;
  -webkit-transition: margin-left 0.3s ease;
  transition: margin-left 0.3s ease;
}
.sidebar:not(.sidebar-open) + .main-content {
  margin-left: 0;
}
.toolbar {
  background: #009688;
  color: #fff;
  padding: 0 20px;
  height: 64px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
}
.menu-btn {
  background: none;
  border: none;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin-right: 15px;
}
.toolbar-title {
  margin: 0;
  font-size: 20px;
  font-weight: normal;
}
.toolbar-spacer {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.github-link {
  color: #fff;
  text-decoration: none;
  padding: 8px 12px;
  border-radius: 4px;
  background: rgba(255,255,255,0.1);
}
.content {
  margin-top: 64px;
  padding: 20px;
}
@media (max-width: 768px) {
.sidebar {
    width: 100%;
}
.sidebar-open + .main-content {
    margin-left: 0;
}
}


